import { type ValueObjectBase } from '@/value_objects';
import { ObjectSchema } from 'yup';


export function yupVOValidation<T, R = T | string>(VO: new (input: R) => ValueObjectBase<T, R>, value: R): boolean {
  try {
    new VO(value);
    return true;
  } catch (_) {
    return false;
  }
}

export type ExtractSchemaFromYup<Y> = Y extends ObjectSchema<infer S> ? S : never;